.root {
  padding: 8px 20px;
  min-height: 88px;

  border-radius: 8px;
  background-color: #ffffff;

  display: flex;
  align-items: center;
  gap: 12px;

  .icon {
    height: 24px;
    width: 24px;
  }
}

.title {
  margin: 0;
  font-family: "Open sans";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.15px;

  color: #263238;
}

.content {
  padding: 0 20px;
  flex: 1;
}

